* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
/* list style */
.country-list {
  list-style: none;
}

.list-item {
  display: flex;
  align-items: center;
}

.list-descr {
  margin-left: 10px;
}

/* card style */
.header {
  font-size: 50px;
}

.card-span {
  font-weight: 700;
}

.card-descr {
  font-size: 35px;
}